@import "_fonts.scss";
@import "_colors.scss";
@import "_typography.scss";

.container {
  max-width: 1170px;
  margin: 0 auto;
}

.header {
  background-color: rgba(16, 16, 29, 0.9);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 9999;

  &__block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    padding: 40px 0;
  }
}

.logo {
  width: 246px;
  height: 20px;

  &__img {
    width: 100%;
  }
}

.nav {
  &__list {
    margin: 0;
    display: flex;
    list-style: none;

    &--open {
      display: flex;
    }
  }

  &__item {
    margin-right: 56px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    color: $le_light;
    font-weight: 600;
    font-size: 0.8125rem;
    text-transform: uppercase;
    text-decoration: none;

    &--active {
      color: $le_accent;
    }

    &:hover {
      color: $le_accent;
      transition: all .3s;
    }
  }
}


.nav-btn {
  max-width: 100%;
  display: none;

  &__button {
    border: 0;
    background: none;
    padding: 18px 15px;
    border-radius: 50%;
    cursor: pointer;
    background: $le_light;
    outline: none;

    &:hover {
      background: $le_accent;
    }
    &:active,
    &:focus {
      outline: none;
    }
    &::-moz-focus-inner {
      border: 0;
    }
  }

  &__line {
    display: block;
    width: 20px;
    height: 2px;
    background: $le_dark;
    margin-bottom: 3px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.first-screen {
  background-image: url('../img/hero-bg.jpg');
  background-size: cover;
  min-height: 100vh;

  &__block {
    display: flex;
    flex-direction: column;
    padding: 150px 0;
  }

  &__text {
    color: $le_dark;
    line-height: 64px;
    margin: 40px 0;
  }
}

.blue {
  color: $le_accent;
}

.info-bl {
  &__text {
    font-size: 1.25rem;
    color: $le_dark;
    font-weight: 700;
    margin: 20px 0;
  }

  &__list {
    font-size: 1.25rem;
    color: $le_dark;
    list-style-type: none;
    margin: 0;
    padding: 0;
    line-height: 34px;
  }

  &__item {
    &::before {
      content: "-";
    }
  }

  &__btns {
    display: flex;
    flex-direction: row;
    margin-top: 77px;
    flex-wrap: wrap;
  }

  &__btn {
    margin-right: 27px;
  }
}

.btn {
  padding: 15px 50px;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 0.8125rem;
  letter-spacing: 0.25px;
  line-height: 1;
  border-radius: 4px;

  &:hover {
    background-color: #286690;
    transition: all .3s;
  }
}


.btn-primary {
  background-color: $le_accent;
  border: 1px solid $le_light;
  color: $le_light;
}

.btn-secondary {
  background-color: $le_light;
  border: 1px solid #6f6d7b;
  color: $le_dark;

  &:hover {
    border: 1px solid $le_light;
    color: $le_light;
  }
}

.orders {
  background: $le_dark;
  background-image: url('../img/map2.png');
  background-size: cover;

  &__block {
    display: flex;
    flex-direction: column;
    padding: 125px 0;
  }

  &__info {
    margin: 80px 0 0;
  }

  &__form {
    margin-top: 90px;
  }
}

.form-order {
  &__top {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 30px 0 0;
  }

  &__range {
    width: 65.5%;
  }

  &__btn {
    width: 31.5%;
    margin: 40px auto 0;
  }
}

.range-block {
  &__slider {
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-box-shadow: none;
    outline: none;
    width: 100%;
    height: 30px;
    padding: 1px 2px;
    //border: 1px solid $le_light;
    border-radius: 5px;
    background: transparent;
    background-image: none;
    -webkit-transition: .2s;
    transition: opacity .2s;
    margin: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      position: relative;
      top: -10px;
      width: 24px;
      height: 24px;
      border: 3px solid #286690;
      border-radius: 50%;
      background: $le_accent;
      cursor: pointer;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }

    &::-webkit-slider-runnable-track {
      border: 1px solid $le_light;
      color: transparent;
      background: rgba(255, 255, 255, .7);
      height: 5px;
      border-radius: 5px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }

    &::-moz-range-thumb {
      width: 24px;
      height: 24px;
      border: 3px solid #286690;
      border-radius: 50%;
      background: $le_accent;
      cursor: pointer;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }

    &::-moz-range-track {
      border: inherit;
      background: transparent;
      background: rgba(255, 255, 255, .7);
      border: 1px solid $le_light;
      height: 5px;
      border-radius: 5px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }

    &::-ms-tooltip {
      display: none;
    }

    &::-ms-thumb {
      position: relative;
      z-index: 999;
      width: 24px;
      height: 24px;
      border: 3px solid #286690;
      border-radius: 50%;
      background: $le_accent;
      margin: 0 4px 0;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }

    &::-ms-fill-lower {
      background: transparent;
    }

    &::-ms-fill-upper {
      background: transparent;
    }

    &::-ms-track {
      border: 1px solid $le_light;
      color: transparent;
      background: rgba(255, 255, 255, .7);
      height: 5px;
      border-radius: 5px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }

    &:hover {
      cursor: pointer;
    }

    &:focus {
      outline: none;
    }
  }

  &__texts {
    display: flex;
    justify-content: space-between;
    font-size: 1.125rem;
  }

  &__percent {
    padding-left: 30px;
    font-weight: 600;

    &::after {
      content: " %";
      width: 20px;
      height: 20px;
      font-weight: 600;
    }
  }
}

input[type='text'],
[type='email'],
select,
textarea {
  background-color: rgba(255, 255, 255, 0.85);
  border: 1px solid $le_light;
  border-radius: 4px;
  color: $le_dark;
  font-size: 1.125rem;
  font-family: 'Lato';
  margin: 0;
  padding: 10px 14px;
  width: 31.5%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;

  &::-webkit-input-placeholder {
    color: $le_dark;
  }

  &::-moz-placeholder {
    color: $le_dark;
  }

  &:-ms-input-placeholder {
    color: $le_dark;
  }

  &:-moz-placeholder {
    color: $le_dark;
  }
}


input[type='text'],
[type='email'],
textarea {
  &:focus {
    border: 1px solid $le_accent;
    box-shadow: 0px 0px 10px $le_accent;
  }
}

.form-btn {
  &__send {
    width: 100%;
    font-size: 0.9375rem;
  }
}

.upload-block {
  display: block;
  width: 31.5%;
  position: relative;

  &__upload {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: 1;
    color: transparent;
    text-shadow: 0 0 0 #2196f3;
    font-family: 'Lato';
  }

  &__label {
    color: $le_dark;
    font-family: 'Lato';
    font-weight: 600;
    border: 2px solid $le_accent;
    background: rgba(255, 255, 255, 0.95);
    box-sizing: border-box;
    font-family: sans-serif;
    width: 100%;
    height: 44px;
    display: block;
    text-transform: uppercase;
    font-size: 0.9375rem;
    margin-top: 0;
    padding-top: 12px;
    border-radius: 4px;
    text-align: center;
  }

  &::before {
    content: "";
    position: absolute;
    background-image: url('../img/document-btn-i.svg');
    background-repeat: no-repeat;
    top: 22%;
    left: 22%;
    width: 18px;
    height: 18px;
  }
}



.section-heading {
  &__text {
    text-align: center;
    margin: 0 0 21px;
  }

  &__subheading {
    text-align: center;
    color: $le_subheading;
    font-size: 1.125rem;
    margin: 0;
  }
}

.order-steps {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    min-width: 180px;

    &:last-child::after {
      content: none;
      background-image: none;
    }

    &::after {
      content: "";
      position: absolute;
      background-image: url('../img/line-gray.svg');
      background-repeat: no-repeat;
      top: 30%;
      right: -47%;
      width: 102px;
      height: 6px;
    }
  }

  &__icon {
    width: 95px;
    max-width: 100%;
    height: 95px;
    border-radius: 999px;
    border: 5px solid #286690;
    background-color: $le_accent;
    display: flex;
    align-items: center;
    align-content: center;
  }

  &__img {
    width: 36px;
    max-width: 100%;
    margin: auto;
  }

  &__txt {
    text-align: center;
    margin-top: 22px;
  }
}


.footer {
  background-color: $le_footerbgc;

  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 30px 0;
  }

  &__copyrights {
    font-size: 0.8125rem;
    position: relative;

    &::before {
      content: "";
      width: 1px;
      height: 18px;
      background-color: $le_footerpay;
      top: 1px;
      right: -14%;
      position: absolute;
    }
  }

  &__partners {
    display: flex;
    position: relative;

    &::before {
      content: "";
      width: 1px;
      height: 18px;
      background-color: $le_footerpay;
      top: 1px;
      right: -14%;
      position: absolute;
    }
  }
}

.partner {
  &__link {
    font-size: 0.8125rem;
    color: $le_footerpay;
    text-decoration: none;
    margin-left: 60px;
    position: relative;

    &:hover {
      color: $le_footerlinkhover;
      transition: all .3s;
    }

    &:first-child {
      margin-left: 0;
    }
  }
}

