.contacts {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  &__link {
    font-size: 0.8125rem;
    color: $le_light;
    text-decoration: none;
    margin-left: 50px;
    position: relative;

    &:hover {
      color: $le_footerlinkhover;
      transition: all .3s;
    }
  }
}

.email {
  &::before {
    content: "";
    position: absolute;
    background-image: url('../img/sprite.png');
    background-position: -30px 0px;
    width: 15px;
    height: 13px;
